*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --background-main: #fcfcfc;
  --text-light: #fcfcfc;
  --text-dark: #131313;
  --text-grey: #ababab;
  --footer-text-grey: #7a7a7a;
  --accent-color: #f8d24e;
  --green-section-background: #84a178;
  --product-green: #aec6a5;
  --circle-green: linear-gradient( 0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7) ), #d9d9d9;
  --circle-green-shadow: 0px 8px 30px rgba(10, 136, 23, .25);
  --product-yellow: #efd478;
  --circle-yellow: linear-gradient( 0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7) ), #d9d9d9;
  --circle-yellow-shadow: 0px 8px 30px rgba(153, 114, 23, .25);
  --product-brown: #d6936d;
  --circle-brown: linear-gradient( 0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7) ), #d9d9d9;
  --circle-brown-shadow: 0px 8px 30px rgba(0, 0, 0, .25);
  --input-fill: rgba(#f8d24e, .1);
  --button-green: #0a8817;
  --button-grey: #ababab;
  --button-yellow: #f8d24e;
  --modal-backdrop-yellow: rgba(248, 210, 78, .2);
  --modal-backdrop-green: rgba(10, 136, 23, .2);
  --modal-backdrop-brown: rgba(213, 117, 63, .2);
  --border-brown: 2px solid #d6936d;
  --border-green: 2px solid #96af8c;
  --border-yellow: 2px solid #f8d24e;
  --border-grey: 2px solid #ababab;
  --input-border: 1px solid #84a178;
  --easeInBack: .6s cubic-bezier(.36, 0, .66, -.56);
  --easeOutCirc: .6s cubic-bezier(0, .55, .45, 1);
  --easeInOutBack: .6s cubic-bezier(.68, -.6, .32, 1.6);
}

body {
  background-color: var(--green-section-background);
  color: var(--text-dark);
  letter-spacing: .04em;
  font-family: Montserrat, Seymour One, Roboto, sans-serif;
  font-size: 14px;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

address {
  font-style: normal;
}

#pointer-dot {
  width: 0;
  height: 0;
  z-index: 201;
  pointer-events: none;
  border: 2.5px solid rgba(10, 136, 23, .01);
  border-radius: 4px;
  transition: border-color .5s;
  position: fixed;
  top: 0;
  left: 0;
}

#pointer-ring {
  width: 0;
  height: 0;
  z-index: 202;
  pointer-events: none;
  border: 2px solid rgba(10, 136, 23, .01);
  border-radius: 100px;
  padding: 15px;
  position: fixed;
  top: 0;
  left: 0;
}

html, a {
  cursor: none !important;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    overflow-x: hidden;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
