.products__sub-title {
  transition: transform 500ms linear;

  &:hover {
    transform: scale(1.2);
  }
}

.products__title {
  transition: transform 500ms linear;

  &:hover {
    transform: scale(1.2);
  }
}

.products {
  background-color: #fff;
}

